import * as models from "~/models";

export const listSchools = async ({ state, city, type }) => {
  const schools = await models.qedu.getSchools(state, city);

  if (type === "publicas") {
    return schools.filter(school => school.isPrivateSchool !== 1);
  }

  if (type === "privadas") {
    return schools.filter(school => school.isPrivateSchool === 1);
  }

  return schools;
};
